<template>
	<view class="wrap">
		<view class="u-tabs-box">
			<u-tabs-swiper activeColor="#06A7E2" inactive-color="#000000" ref="tabs" :list="list" :current="current"
				@change="change" bar-width="100" :is-scroll="false" swiperWidth="750">
			</u-tabs-swiper>
		</view>
		<swiper class="swiper-box" :current="swiperCurrent" @transition="transition" @animationfinish="animationfinish">
			<swiper-item>
				<view class="swiper-item">
					<u-form :model="form1" ref="basicForm" label-width="200" label-align="center"
						:border-bottom="false">
						<u-form-item label="申请人" class="form1-item">
							<u-input v-model="form1.name" placeholder="请输入申请人姓名" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="申请部门" class="form1-item">
							<u-input v-model="form1.depart" placeholder="请输入申请部门" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="归属公司" class="form1-item">
							<u-input v-model="form1.company" placeholder="请输入归属公司" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="业务部门" class="form1-item">
							<u-input v-model="form1.depart2" placeholder="请输入业务部门" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="预算科目" class="form1-item">
							<u-input v-model="form1.subject" placeholder="请输入预算科目" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="业务说明" class="form1-item">
							<u-input v-model="form1.descrp" placeholder="请输入业务说明" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="结算方式" class="form1-item">
							<u-input v-model="form1.pattern" placeholder="请输入结算方式" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="金额" class="form1-item">
							<u-input v-model="form1.number" placeholder="请输入金额" type="text" border="border"
								height="40" />
						</u-form-item>
						<u-form-item label="研发订单" class="form1-item">
							<u-input v-model="form1.order" placeholder="请输入订单号" type="text" border="border"
								height="40" />
						</u-form-item>
					</u-form>
				</view>
				<view class="submitBtn" @tap="submit">
					<view class="submit">提交</view>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="page-box">
					<view class="order" v-for="(res, index) in orderList" :key="res.id">
						<view class="left">
							<view class="purchaser">购买方：{{ res.purchaser }}</view>
							<view class="seller">销售方：{{ res.seller }}</view>
							<view class="contact">
								联系电话：{{ res.contact }}
							</view>
						</view>
						<view class="right">
							<view class="price">￥{{ res.price }}</view>
						</view>
						
					</view>
					<u-loadmore status="loadmore" bgColor="#f2f2f2"></u-loadmore>
					<u-popup v-model="popShow" mode="bottom">
						<view class="importBtn">
							<u-button hair-line="false">从我的票夹导入</u-button>
							<u-button hair-line="false">直接拍照导入</u-button>
							<u-button @tap="popShow = false" hair-line="false">取消</u-button>
						</view>
					</u-popup>
					<view class="addMore" @tap="popShow = true">
						<view class="add">
							<u-icon name="plus" class="icon" :size="50"></u-icon>添加明细	
						</view>
					</view>
				</view>
			</swiper-item>
			<swiper-item>
				<view class="swiper-item">
					<u-form :model="form2" ref="cashForm" label-width="200" label-align="center">
						<u-form-item label="收款人" class="form2-item">
							<u-input v-model="form2.name" placeholder="请输入收款人姓名" type="text" border="border"
								height="60" />
						</u-form-item>
						<u-form-item label="收款银行" class="form2-item">
							<u-input v-model="form2.bank" placeholder="请输入收款银行" type="text" border="border"
								height="60" />
						</u-form-item>
						<u-form-item label="收款账号" class="form2-item">
							<u-input v-model="form2.account" placeholder="请输入收款账号" type="text" border="border"
								height="60" />
						</u-form-item>
						<u-form-item label="收款金额" class="form2-item">
							<u-input v-model="form2.number" placeholder="请输入收款金额" type="text" border="border"
								height="60" />
						</u-form-item>
						<u-form-item label="开户代码" class="form2-item">
							<u-input v-model="form2.code" placeholder="请输入开户代码" type="text" border="border"
								height="60" />
						</u-form-item>
						<u-form-item label="对公对私" class="form2-item">
							<u-select v-model="show" mode="single-column" :list="selectionList" @confirm="confirm">
							</u-select>
							<u-input v-model="selectLabel" type="select" @click="show = true" />
						</u-form-item>
					</u-form>
				</view>
				<view class="submitBtn" @tap="submit">
					<view class="submit">提交</view>
				</view>
			</swiper-item>
		</swiper>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				list: [{
						name: '基础信息'
					},
					{
						name: '报销明细'
					},
					{
						name: '收款信息'
					}
				],
				selectionList: [{
						value: '0',
						label: '对公'
					},
					{
						value: '1',
						label: '对私'
					},
				],
				form1: {
					name: '',
					dapart: '',
					company: '',
					depart2: '',
					subject: '',
					descrp: '',
					pattern: '',
					number: 0,
					order: ''
				},
				form2: {
					name: '',
					bank: '',
					account: '',
					number: 0,
					code: '',
					isCommon: ''
				},
				orderList: [
					{
						purchaser: '杰克缝纫机股份有限公司',
						seller: 'XXX酒店',
						contact: '1345867291',
						price: '1200'
					},
					{
						purchaser: '杰克缝纫机股份有限公司',
						seller: 'XXX酒店',
						contact: '1345867291',
						price: '1200'
					},
					{
						purchaser: '杰克缝纫机股份有限公司',
						seller: 'XXX酒店',
						contact: '1345867291',
						price: '1200'
					}
				],
				popShow: false,
				show: false,
				current: 0,
				swiperCurrent: 0,
				selectLabel: '对公'
			}
		},
		methods: {
			// tab栏切换
			change(index) {
				this.swiperCurrent = index;
				// this.getOrderList(index);
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.tabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.tabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
			confirm(e) {
				this.selectLabel = e[0].label;
				this.form2.isCommon = e[0].value;
				console.log(this.form2.isCommon);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.form1-item {
		height: 50px;
	}

	.form2-item {
		height: 60px;
	}

	.wrap {
		width: 100%;
		height: 100%;
	}

	.swiper-box {
		width: 700rpx;
		background-color: #ffffff;
		margin: 20rpx auto;
		border-radius: 20rpx;
		padding: 10rpx;
		font-size: 28rpx;
		height: 90%;
	}

	.swiper-item {
		height: 100%;
	}

	.order {
		width: 710rpx;
		background-color: #f2f2f2;
		margin: 20rpx auto;
		display: flex;
		border-radius: 20rpx;
		box-sizing: border-box;
		padding: 20rpx;
		font-size: 28rpx;
		
		.left {
			.purchaser {
				margin: 0 10rpx;
				font-size: 32rpx;
			}
			
			.seller, .contact {
				margin: 30rpx 10rpx;
				font-size: 10rpx;
			}
		}
		
		.right{
			.price {
				margin: 50rpx 24rpx auto;
			}
		}


		.item {
			display: flex;
			margin: 20rpx 0 0;

			.left {
				margin-right: 20rpx;

				image {
					width: 200rpx;
					height: 200rpx;
					border-radius: 10rpx;
				}
			}

			.content {
				.title {
					font-size: 28rpx;
					line-height: 50rpx;
				}

				.type {
					margin: 10rpx 0;
					font-size: 24rpx;
					color: $u-tips-color;
				}

				.delivery-time {
					color: #e5d001;
					font-size: 24rpx;
				}
			}

			.right {
				margin-left: 10rpx;
				padding-top: 20rpx;
				text-align: right;

				.decimal {
					font-size: 24rpx;
					margin-top: 4rpx;
				}

				.number {
					color: $u-tips-color;
					font-size: 24rpx;
				}
			}
		}

		.total {
			margin-top: 20rpx;
			text-align: right;
			font-size: 24rpx;

			.total-price {
				font-size: 32rpx;
			}
		}

		.bottom {
			display: flex;
			margin-top: 40rpx;
			padding: 0 10rpx;
			justify-content: space-between;
			align-items: center;

			.btn {
				line-height: 52rpx;
				width: 160rpx;
				border-radius: 26rpx;
				border: 2rpx solid $u-border-color;
				font-size: 26rpx;
				text-align: center;
				color: $u-type-info-dark;
			}

			.evaluate {
				color: $u-type-warning-dark;
				border-color: $u-type-warning-dark;
			}
		}
	}

	.addMore {
		display: flex;
		justify-content: space-around;
		margin: 30rpx auto;
		line-height: 100rpx;
		font-size: 50rpx;
		.add{
			display: flex;
			align-items: center;
			.icon{
				margin-right: 10rpx;
			}
		}
	}
	
	.submitBtn {
		display: flex;
		justify-content: space-around;
		width: 600rpx;
		line-height: 100rpx;
		position: absolute;
		bottom: 30rpx;
		left: 70rpx;
		background-color: #06A7E2;
		border-radius: 60rpx;
		font-size: 30rpx;
		.submit{
			display: flex;
			align-items: center;
			color: #ffffff;
		}
	}
</style>
